import { Component, OnInit } from '@angular/core';
import { TitleCasePipe } from '@angular/common';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ScheduleShopService } from 'src/app/core/services/schedule-shop.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-shop-details-page',
  templateUrl: './shop-details-page.component.html',
  styleUrls: ['./shop-details-page.component.scss']
})
export class ShopDetailsPageComponent implements OnInit {

  shops=[
    {
      "id":1,
      "email": "demo21@gmail.com",
      "mobile": "8783459890",
      "firstName": "john",
      "lastName": "hi",
      "parentId": "478d3543-d30d-43f2-9559-a080f7b93025",
      "isRegistered": true,
      "legalName": "NH_SHOP",
      "accountMethod": "Accounts With Stock Management",
      "businessType": "Proprietorship",
      "gstinNo": "22",
      "gstType": "Regular",
      "compositeType": "Trader",
      "homeno": "2",
      "pincode": "390011",
      "address": "10,abcd road",
      "city": "vadodara",
      "district": "vadodara",
      "landmark": "vadodara",
      "state": "GUJARAT",
      "country": "INDIA"
    }  
  ]

  public shopForm: FormGroup;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  public ShopListResponse:any;
  isActive: boolean;

  constructor(private formBuilder:FormBuilder ,private ScheduleShopService:ScheduleShopService,private toastService:ToastNotificationService,
    private titleCasePipe: TitleCasePipe) { }

  ngOnInit(): void {
    this.getShopList();
  }

  getShopList(){
    this.ScheduleShopService.getShopList({},this.currentUser.id).then((res) => {
      if(res.data.rows){
        this.ShopListResponse = res.data.rows;
        this.isActive = res.data.rows.map(data => {
          data.isActive
        })
        console.log(this.ShopListResponse)
      }
    },(err)=>{
      if(err.error.expose){
        this.ShopListResponse = [];
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else{
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })
  }

  toggleActiveStatus(shopId) {
    const shopToUpdate = this.ShopListResponse.find(shop => shop.id === shopId);

    const updatedShop = { ...shopToUpdate, isActive: !shopToUpdate.isActive }; // Create a copy and toggle isActive
    
    this.ScheduleShopService.UpdateShopDetails(updatedShop, shopId).then(
      (res : any) => {
        if(res.success){
          console.log('Shop updated successfully.')
          this.getShopList()
        }
    })
  }

}

