import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-updateappointment',
  templateUrl: './updateappointment.component.html',
  styleUrls: ['./updateappointment.component.scss']
})
export class UpdateappointmentComponent implements OnInit {
  public submitted: boolean;
  isActive: boolean;
  public ShopListResponse: any;

  public addAppointmentform: any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  shopID: any;

  locations = [];

  userRoles = [
    {
      "id": 1,
      "userRole": "Admin"

    },
    {
      "id": 1,
      "userRole": "User"

    }
  ]
  constructor(private formBuilder: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    public dialogRef: MatDialogRef<UpdateappointmentComponent>) { }

  ngOnInit(): void {
    this.addAppointmentform = this.formBuilder.group({

      name: ['', Validators.required],
      duration: ['', Validators.required],
      LocationType: ["", [Validators.required]],
      UserRole: ["", Validators.required],
      Description: ["", Validators.required],
      Cancellation: [false, Validators.required],
      isDisable: [false, Validators.required],
      withinDays: ["", [Validators.required]]
    });
    this.route.params.subscribe(
      params => this.shopID = params['id']
    );
   }
  get f() {
    return this.addAppointmentform.controls;
  }
  getAppointmentType(){
    
  }

  cancel() {
    // Reset the form and submitted flag
    this.addAppointmentform.reset();
    this.submitted = false;
    this.dialogRef.close();

  }
}


